import wxapi from "@/api/api.js"

export default {
    name: "main",
    data() {
        return {
            tableData: [],
            currentPage: 1,
            pageSize: 10,
            total: 11
        }
    },
    created() {
        this.findPage();
    },
    methods: {
        async findPage() {
            let re = await wxapi.findAll(this.currentPage, this.pageSize)
            this.tableData = re.records;
            this.total = re.total;
        },
        deleteById(data) {
            let id = [data.id];
            wxapi.deleteById(id)
            this.findPage();
        },
        async send(data) {
            let id = [data.id];
            await wxapi.send(id);
            this.findPage();
        },
        /**
         * 分页改变
         * @param page
         */
        pageChange(page) {
            this.currentPage = page;
            this.findPage()
        }


    }
}